import React from "react";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
export default function Camera360({ CourseList }) {
     let courseRender = CourseList.courseCamera;
     const [render, setRender] = useState(() => {
          const PTS = JSON.parse(window.localStorage.getItem("render1"));
          return PTS || [];
     });
     const handleGim = (e, course, index) => {
          const b = document.querySelectorAll(".cs__container");
          b[index].classList.toggle("clicked");
          // b[0].classList.toggle(`order${index + 1}`);
          b[index].classList.toggle("order1");
          if (render.indexOf(index) > -1) {
               const r = render.filter((x) => x !== index);
               setRender(r);
          } else {
               setRender([...render, index]);
          }
          // _____________________________________
     };
     useEffect(() => {
          render.forEach((item) => {
               const b = document.querySelectorAll(".cs__container");
               b[item].classList.add("clicked");
               b[item].classList.add("order1");
               b[0].classList.add(`order${item + 1}`);
          });
          let a = JSON.stringify(render);
          window.localStorage.setItem("render1", a);
     });
     useEffect(() => {
          const C360 = JSON.parse(window.localStorage.getItem("360"));
          const FI = JSON.parse(window.localStorage.getItem("FI"));
          const CV = JSON.parse(window.localStorage.getItem("CV"));
          const CA = JSON.parse(window.localStorage.getItem("CA"));
          let W0 = ~~((C360 * 100) / (CourseList.courseYoutube360.length - 1)); // cv
          let W1 = ~~((FI * 100) / (CourseList.courseYoutubeFI.length - 1)); // cv
          let W2 = ~~((CV * 100) / (CourseList.courseYoutubeCV.length - 1)); // cv
          let W3 = ~~((CA * 100) / (CourseList.courseYoutubeCA.length - 1)); // cv
          let MyW = [W0, W1, W2, W3];
          console.log(MyW);
          MyW.forEach((item, i) => {
               const spanWithd = document.querySelectorAll(".cs__img span");
               spanWithd[i + 1] && (spanWithd[i + 1].style.width = `${item}%`);
               if (item < 100) {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i + 1] && (b[i + 1].innerText = `${item}%`);
               } else {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i + 1] && (b[i + 1].innerText = "Đã hoàn thành");
               }
          });
     });
     return (
          <div className="cs__full">
               <p className="cs__start">
                    <i className="fas fa-camera-retro"></i>
                    <span> Bắt đầu làm ảnh chuyên nghiệp hơn</span>
               </p>
               <div className="cs__oderbox">
                    {courseRender.map((course, index) => (
                         <div className="cs__container" key={index}>
                              <i
                                   onClick={(e) => handleGim(e, course, index)}
                                   className="fas fa-star cs__mask"
                                   title="Vào học đi kìa"
                              ></i>
                              <Link to={course.to} className="cs__img">
                                   <img src={course.srcImg} alt="lỗi ảnh" />
                                   <span></span>
                                   <b>0%</b>
                              </Link>
                              <p className="cs__content">{course.name}</p>

                              {/*
                              // href="https://www.youtube.com/watch?v=C6m_QYL-Lt0&list=PLH88ngaKjRaQdJkYizDyCE_5Bpcfk9DEO"
                              // target="_blank"
                              // rel="noReferrer"
                              className="cs__btn"
                         >
                              <i className="fas fa-pen-alt"></i>
                              <span>Học ngay</span>
                         </Link> */}
                         </div>
                    ))}
               </div>
          </div>
     );
}
