import React, { memo } from "react";
import { NavLink, Link } from "react-router-dom";
import Text from "../TEXT";
import "./Header.scss";
function Header() {
     const navList = Text.navSide;
     window.addEventListener("scroll", () => {
          const headerScroll = document.querySelector(".header__scroll");
          const wY = window.scrollY;
          if (wY < 10) {
               const head = document.querySelector(".header");
               head && head.classList.add("active");
          } else {
               const head = document.querySelector(".header");
               head.classList.remove("active");
          }
          var winScroll =
               document.body.scrollTop || document.documentElement.scrollTop;
          var height =
               document.documentElement.scrollHeight -
               document.documentElement.clientHeight;
          var scrolled = (winScroll / height) * 100;
          headerScroll.style.width = scrolled + "%";
          console.log(scrolled);
          // if (scrolled > 100) {
          //      setTimeout(() => {
          //           window.scrollTo({
          //                top: height - 80,
          //                behavior: "smooth",
          //           });
          //      }, 100);
          // }
     });

     const handleClick = (e, title) => {
          const navMobile = document.querySelector(".header__list");
          const navBar = document.querySelector(".header__bar");
          const navBarClose = document.querySelector(".header__barclose");
          window.scroll(0, 0);
          document.title = title || "Kỹ thuật";
          navMobile.classList.toggle("active1");
          navBar.classList.remove("active1");
          navBarClose.classList.toggle("active1");
     };
     const handleBackNav = () => {
          window.history.back();
     };
     const handleNav = (e) => {
          const navMobile = document.querySelector(".header__list");
          const navBar = document.querySelector(".header__bar");
          const navBarClose = document.querySelector(".header__barclose");
          navBar.classList.toggle("active1");
          navBarClose.classList.toggle("active1");
          navMobile.classList.toggle("active1");
     };

     return (
          <div className="header active">
               <span className="header__scroll"></span>
               <span onClick={handleBackNav} className="header__back">
                    <i className="fas fa-chevron-circle-left"></i>{" "}
                    <span>Quay lại</span>
               </span>
               <Link to="/">
                    <img
                         className="header__fixlogo none"
                         src="https://github.com/mamxanhtinhnguyen/Images/blob/main/M%E1%BA%A7m%20Xanh/M%E1%BA%A6M%20XANH%20(LOGO)%20TRON%20NHO.png?raw=true"
                         alt="Logo"
                    />
               </Link>
               <span className="header__mam none">
                    MẦM XANH <span className="header__course"> </span>
               </span>

               {/* <NavLink to="/">
                    <h1 className="header__h1">MẦM XANH</h1>{" "}
               </NavLink> */}

               <ul className="header__list">
                    {navList.map((item, index) => (
                         <li key={index}>
                              <NavLink
                                   exact={item.exact}
                                   activeClassName="active1"
                                   to={item.id}
                                   onClick={(e) => handleClick(e, item.title)}
                                   className="header__list--item"
                              >
                                   <i className={item.icon}> </i> {"   "}
                                   {item.title}
                              </NavLink>
                         </li>
                    ))}
                    <NavLink
                         to="/kythuat"
                         activeClassName="active2"
                         className="header__plus-icon"
                         onClick={(e) => handleClick()}
                    >
                         <i className="fas fa-book"></i>
                    </NavLink>
                    {/* <h1 className="header__title__h1">Đội tình nguyện Mầm Xanh</h1> */}
               </ul>
               <div onClick={(e) => handleNav(e)} className="header__overlay"></div>
               <div onClick={(e) => handleNav(e)} className="header__bar">
                    <i className="fas fa-bars"></i>
               </div>

               <div className="header__barclose active1">
                    <i className="fas fa-times"></i>
               </div>
          </div>
     );
}
export default memo(Header);
