import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Courses from "../../src/components/COURSE";
import AvatarCover from "../components/AvatarCover/AvatarCover";
import Course from "../components/Course/Course";
import Camera360 from "../components/Cs6/Camera360";
import Cs6 from "../components/Cs6/Cs6";
import Tips from "../components/Cs6/Tip";
import LogoPNG from "../components/LogoPNG/LogoPNG";
import Skill from "../components/Skill/Skill";
import Remove from "../components/Webs/Remove";
import Webs from "../components/Webs/Webs";

export default function SkillContain() {
     return (
          <div>
               <Router>
                    <Skill />
                    <Route exact path="/kythuat">
                         <AvatarCover />
                    </Route>
                    <Route exact path="/kythuat/logo">
                         <LogoPNG />
                    </Route>
                    <Route exact path="/kythuat/webs">
                         <Webs />
                    </Route>
                    {/* <Route exact path="/kythuat/silk">
                         <Silk />
                    </Route> */}
                    <Route exact path="/kythuat/removal">
                         <Remove />
                    </Route>
                    <Route exact path="/kythuat/cs6">
                         <Cs6 CourseList={Courses} courseRender="render" />
                    </Route>
                    <Route exact path="/kythuat/camera">
                         <Camera360 CourseList={Courses} />
                    </Route>
                    <Route exact path="/kythuat/tips">
                         <Tips CourseList={Courses} />
                    </Route>

                    {/* __________________________________Course ________________________ */}
                    <Route exact path="/kythuat/course">
                         <Course
                              store="PTS"
                              record="rPTS"
                              name="Làm chủ Photoshop [ Ps ] "
                              courses={Courses.courseYoutubePTS}
                              keyboard={Courses.courseKeyPTS}
                         />
                    </Route>
                    <Route exact path="/kythuat/ai">
                         <Course
                              store="AI"
                              record="rAI"
                              name="Adobe Illustrator 2020 [ Ai ]  "
                              courses={Courses.courseYoutubeAI}
                              keyboard={Courses.courseKeyAI}
                         />
                    </Route>
                    <Route exact path="/kythuat/pr">
                         <Course
                              store="PR"
                              record="rPR"
                              name="[ Pr ] Adobe Premiere"
                              courses={Courses.courseYoutubePR}
                              keyboard={Courses.courseKeyPR}
                         />
                    </Route>
                    <Route exact path="/kythuat/fi">
                         <Course
                              store="FI"
                              record="rFI"
                              name="Wondershare Filmora X"
                              courses={Courses.courseYoutubeFI}
                              back="/kythuat/camera"
                         />
                    </Route>

                    <Route exact path="/kythuat/au">
                         <Course
                              store="AU"
                              record="rAU"
                              name=" Adobe Audition [ Au ] "
                              courses={Courses.courseYoutubeAU}
                         />
                    </Route>
                    <Route exact path="/kythuat/cv">
                         <Course
                              store="CV"
                              record="rCV"
                              name="Canva Pro 2021"
                              courses={Courses.courseYoutubeCV}
                              back="/kythuat/camera"
                         />
                    </Route>
                    <Route exact path="/kythuat/360">
                         <Course
                              store="360"
                              record="r360"
                              name="Nhiếp ảnh 360 cho người mới"
                              courses={Courses.courseYoutube360}
                              back="/kythuat/camera"
                         />
                    </Route>
                    <Route exact path="/kythuat/ca">
                         <Course
                              store="CA"
                              record="rCA"
                              name=" Làm Video A - Z với Camtasia"
                              courses={Courses.courseYoutubeCA}
                              keyboard={Courses.courseKeyCA}
                              back="/kythuat/camera"
                         />
                    </Route>
                    <Route exact path="/kythuat/lr">
                         <Course
                              store="LR"
                              record="rLR"
                              name="Học Lightroom"
                              courses={Courses.courseYoutubeLR}
                              keyboard={Courses.courseKeyLR}
                         />
                    </Route>
                    <Route exact path="/kythuat/ae">
                         <Course
                              store="AE"
                              record="rAE"
                              name="Khoá học After Effect"
                              courses={Courses.courseYoutubeAE}
                              keyboard={Courses.courseKeyAE}
                         />
                    </Route>
                    <Route exact path="/kythuat/pstip">
                         <Course
                              store="PSTIP"
                              record="rPTSTIP"
                              name="CS6 Tips +Poster MV"
                              courses={Courses.courseYoutubePSTIP}
                              back="/kythuat/tips"
                         />
                    </Route>
                    <Route exact path="/kythuat/pstt">
                         <Course
                              store="PSTT"
                              record="rPSTT"
                              name="CS6 Effect Tutorial"
                              courses={Courses.courseYoutubePSTT}
                              back="/kythuat/tips"
                         />
                    </Route>
               </Router>
          </div>
     );
}
