import React from "react";
import "./Course.scss";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
export default function Course({
     courses,
     name,
     store,
     keyboard,
     record,
     back = "/kythuat/cs6",
}) {
     const listSeen = document.querySelectorAll(".course__number--item");
     const [seen, setSeen] = useState(() => {
          const seenID = JSON.parse(window.localStorage.getItem(store));
          return seenID || 0;
     });
     const [crr, setCrr] = useState(seen);
     const [per, setPer] = useState(0);
     useEffect(() => {
          const header = document.querySelector(".header");
          const headerC = document.querySelector(".header__course");
          const headerCL = document.querySelector(".header__color");
          const headerB = document.querySelector(".header__back");
          const logo = document.querySelector(".header__fixlogo.none");
          const mam = document.querySelector(".header__mam.none");
          const side = document.querySelector(".nav__side");
          logo && logo.classList.remove("none");
          mam && mam.classList.remove("none");
          headerCL && headerCL.classList.remove("none");
          headerB.classList.add("none");
          side.classList.add("none");
          header.classList.add("black");
          headerC.innerText = name;

          return () => {
               headerB.classList.remove("none");
               header.classList.remove("black");
               const logo = document.querySelector(".header__fixlogo");
               const mam = document.querySelector(".header__mam");
               const side = document.querySelector(".nav__side");

               logo && logo.classList.add("none");
               mam && mam.classList.add("none");
               headerCL && headerCL.classList.add("none");
               side && side.classList.remove("none");
          };
     });
     useEffect(() => {
          const listSeen = document.querySelectorAll(".course__number--item");
          const study = document.querySelector(".course__title i");
          var percents = ~~((seen / (listSeen.length - 1)) * 100);
          study && (study.style.width = `${percents}%`);
          setPer(percents);
     }, [seen]);

     useEffect(() => {
          const listSeen = document.querySelectorAll(".course__number--item");
          listSeen[seen].classList.add("active1");
          for (let i = 0; i < seen + 1; i++) {
               const listSeen = document.querySelectorAll(".course__number--item");
               listSeen[i].classList.add("active");
               let y = i;
               y++;
               listSeen[y] && listSeen[y].classList.remove("disable");
          }
     }, [seen]);
     const listscroll = document.querySelector(
          ".course__number--item.active.active1",
     );
     listscroll &&
          listscroll.scrollIntoView({
               behavior: "smooth",
               block: "center",
          });

     useEffect(() => {
          const a = seen;
          const seenID = JSON.stringify(a);
          window.localStorage.setItem(store, seenID);
     }, [seen, store]);
     const [youtube, setYoutube] = useState(courses[seen].linkY);
     const handleCourse = (e, course, index) => {
          const listItemActive = document.querySelector(
               ".course__number--item.active1",
          );
          listItemActive && listItemActive.classList.remove("active1");
          e.target.classList.add("active");
          e.target.classList.add("active1");
          setYoutube(course.linkY);
          setCrr(index);
          const listSeen = document.querySelectorAll(".course__number--item");
          listSeen[index + 1] && listSeen[index + 1].classList.remove("disable");
          listSeen[index + 1] && listSeen[index + 1].classList.add("unlock");
          if (index > seen) {
               setSeen(index);
          }
     };
     const handleClose = (e) => {
          const t = window.localStorage.getItem(record);
          const b = document.querySelector(".course__record-input");
          if (b.value.length > 0 && b.value !== t) {
               const c = document.querySelector(".ques__container");
               c && c.classList.add("active");
          } else {
               const b = document.querySelector(".course__record.active");
               b && b.classList.remove("active");
          }
     };
     const handleShow = (e) => {
          const b = document.querySelector(".course__record");
          b && b.classList.add("active");
          const c = document.querySelector(".course__record-key");
          c && c.classList.remove("active");
     };
     useEffect(() => {
          const t = window.localStorage.getItem(record);
          const b = document.querySelector(".course__record-input");
          b.value = t;
     }, [record]);
     const handleSub = (e) => {
          const b = document.querySelector(".course__record-input");
          window.localStorage.setItem(record, b.value);
          const d = document.querySelector(".course__record span");
          const a = document.querySelector(".record__save");
          a.classList.add("saved");

          const c = document.querySelector(".course__record.trans");
          c && c.classList.remove("trans");
          setTimeout(() => {
               d.innerText = "Đã lưu";
          }, 300);
          setTimeout(() => {
               const c = document.querySelector(".course__record");
               c && c.classList.remove("active");
               a.classList.remove("saved");
               d.innerText = "Save";
          }, 700);
     };
     const handleKey = (e) => {
          const b = document.querySelector(".course__record-key");
          b && b.classList.toggle("active");
          const c = document.querySelector(".course__record");
          c && c.classList.remove("active");
     };
     const handleQclosed = (e) => {
          const b = document.querySelector(".course__record.active");
          b && b.classList.remove("active");
          const c = document.querySelector(".ques__container.active");
          c && c.classList.remove("active");
     };
     const handleQsaved = (e) => {
          const c = document.querySelector(".ques__container.active");
          c && c.classList.remove("active");
          handleSub();
     };
     const handleTrans = (e) => {
          const c = document.querySelector(".course__record");
          c.classList.toggle("trans");
     };

     // const handleShot = (e) => {
     //      window.addEventListener("keydown", (e) => {
     //           console.log(e);
     //      });

     //      window.dispatchEvent(
     //           new KeyboardEvent("keydown", {
     //                ctrlKey: true,
     //                code: "KeyS",
     //                // key: "e",
     //                // keyCode: 69,

     //                // which: 69,
     //           }),
     //      );
     //      console.log("ok");
     // };
     const handleFull = (e) => {
          document.querySelector(".header").classList.toggle("none");
          document.querySelector(".remember").classList.toggle("none");
          document.querySelector(".course__container").classList.toggle("full");
     };
     let IF = crr;
     const handleFullNext = (e) => {
          const listItemActive = document.querySelectorAll(".course__number--item");
          IF++;

          if (IF > listItemActive.length - 1) {
               IF = listItemActive.length - 1;
          }
          listItemActive[IF] && listItemActive[IF].click();
     };
     const handleFullPrev = (e) => {
          const listItemActive = document.querySelectorAll(".course__number--item");
          IF--;

          if (IF < 1) {
               IF = 0;
          }
          listItemActive[IF] && listItemActive[IF].click();
     };
     return (
          <div className="course__container">
               <div className="course__record active">
                    <i
                         onClick={handleClose}
                         className="fas fa-times record__close"
                    ></i>
                    <p className="course__record-title">
                         Nội dung ghi nhớ khoá <strong> {name} </strong>
                    </p>
                    <textarea
                         placeholder="Nhập ghi chú của bạn ở đây..."
                         className="course__record-input"
                    ></textarea>
                    <div onClick={handleSub} className="record__save">
                         <i className="fas fa-save "></i> {"  "}
                         <span>Save</span>
                    </div>
                    <div onClick={handleTrans} className="record__trans">
                         <i className="fas fa-adjust"></i>
                         <span>Trong suốt</span>
                    </div>
                    <div className="ques__container">
                         <div className="record__ques active">
                              <p>Bạn có muốn lưu lại không ? </p>
                              <div className="record__ques--buttons">
                                   <div
                                        onClick={handleQsaved}
                                        className="record__ques--item"
                                   >
                                        Save
                                   </div>
                                   <div
                                        onClick={handleQclosed}
                                        className="record__ques--item"
                                   >
                                        Không
                                   </div>
                              </div>
                         </div>
                    </div>
               </div>
               {/* Phim tat */}
               <div className="course__record-key">
                    <i
                         onClick={handleKey}
                         className="fas fa-times record__close"
                    ></i>
                    <p className="course__record-title">
                         Bảng phím tắt trong <strong> {name} </strong>
                    </p>
                    <textarea
                         value={keyboard}
                         readOnly
                         className="course__record-input"
                    ></textarea>
               </div>
               <div className="course__nav">
                    <ul className="course__nav--list">
                         <li onClick={handleShow} className="course__nav--list-item">
                              <i className="fas fa-feather-alt"></i>
                              <span>Ghi chú</span>
                         </li>
                         {"     "}
                         <li onClick={handleKey} className="course__nav--list-item">
                              <i className="fas fa-keyboard"></i>
                              <span>Phím tắt</span>
                         </li>
                         <li
                              onClick={handleFull}
                              className="course__nav--list-item none767"
                         >
                              <i className="fas fa-tv"></i>
                              <span>Màn hình</span>
                         </li>
                         <li
                              onClick={handleFullPrev}
                              className="course__nav--list-item none767"
                         >
                              <i className="fas fa-sort-up"></i>
                         </li>
                         <li
                              onClick={handleFullNext}
                              className="course__nav--list-item none767"
                         >
                              {crr + 1}/{courses.length}
                              <i className="fas fa-sort-down"></i>
                         </li>
                         {/* <li onClick={handleShot} className="course__nav--list-item">
                              <i className="fas fa-feather-alt"></i>
                              <span>Ghi chú</span>
                         </li>{" "} */}
                    </ul>
               </div>

               <ul className="course__number">
                    <li className="course__title">
                         <i></i>
                         <Link
                              to={back}
                              title="Trở về"
                              className="far fa-caret-square-left course__back"
                         ></Link>
                         <span className="course__name">{name}</span>
                         <span className="course__percents">
                              <b className="fas fa-medal"></b> {"   "}
                              Hoàn thành{" "}
                              <strong>
                                   {seen + 1}/{listSeen.length}
                              </strong>{" "}
                              bài học (<strong>{per}%</strong>)
                         </span>
                    </li>
                    <ul className="course__scoll">
                         {courses.map((course, index) => (
                              <li
                                   key={index}
                                   onClick={(e) => handleCourse(e, course, index)}
                                   className="course__number--item disable"
                              >
                                   <span className="course__number--item-title">
                                        {course.title}{" "}
                                        {/* <div className="course__duration">
                                             <i className="fas fa-play-circle"></i>
                                             {"  "}
                                             {course.duration}
                                        </div> */}
                                   </span>
                                   <span className="course__icon">
                                        <i className="fas fa-check"></i>
                                   </span>

                                   <span className="course__unlock">
                                        <i className="fas fa-unlock-alt"></i>
                                   </span>
                                   <span className="course__lock">
                                        <i className="fas fa-lock"></i>
                                   </span>
                              </li>
                         ))}
                    </ul>
               </ul>

               <div className="course__youtube">
                    <iframe
                         id="movie_player"
                         src={`https://www.youtube.com/embed/${youtube}?autoplay=1`}
                         title="YouTube video player"
                         frameBorder="0"
                         allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                         allowFullScreen="1"
                         className="iframe"
                    ></iframe>
               </div>
          </div>
     );
}
