import React from "react";
import { NavLink } from "react-router-dom";

import "./Skill.scss";
export default function Skill() {
     const handleShow = () => {
          const c = document.querySelector(".skill__container");
          c.classList.toggle("show");
     };
     const handleShowRemove = () => {
          const c = document.querySelector(".skill__container.show");
          c && c.classList.remove("show");
     };
     return (
          <>
               <div className="skill__container">
                    <div className="skill__avatar">
                         <div onClick={() => handleShow()} className="skill__show">
                              <i className="fas fa-book left"></i>
                              <i className="fas fa-caret-right right"></i>
                         </div>

                         <NavLink
                              onClick={handleShowRemove}
                              exact
                              activeClassName="active1"
                              to="/kythuat"
                              className="skill__button"
                         >
                              <i className="fas fa-user-circle"></i>{" "}
                              <span>Avatar qua các năm</span>
                         </NavLink>
                         <NavLink
                              onClick={handleShowRemove}
                              activeClassName="active1"
                              to="/kythuat/logo"
                              className="skill__button"
                         >
                              <i className="fas fa-leaf"></i> <span>Logo PNG</span>
                         </NavLink>
                         <NavLink
                              onClick={handleShowRemove}
                              activeClassName="active1"
                              to="/kythuat/removal"
                              className="skill__button"
                         >
                              <i className="fas fa-photo-video"></i>{" "}
                              <span>Tách nền tự động</span>
                         </NavLink>
                         <NavLink
                              onClick={handleShowRemove}
                              exact
                              activeClassName="active1"
                              to="/kythuat/webs"
                              className="skill__button"
                         >
                              <i className="fas fa-palette"></i>
                              {"    "}
                              <span>Mã màu Gradient đẹp</span>
                         </NavLink>
                         <br />
                         <h6 className="skill__h6">Bắt đầu hành trình Designer</h6>
                         <NavLink
                              onClick={handleShowRemove}
                              exact
                              activeClassName="active1"
                              to="/kythuat/cs6"
                              className="skill__button w"
                         >
                              <i className="fas fa-book-open"></i>{" "}
                              <span>Làm quen bộ Adobe App</span>
                         </NavLink>
                         <NavLink
                              onClick={handleShowRemove}
                              exact
                              activeClassName="active1"
                              to="/kythuat/camera"
                              className="skill__button w"
                         >
                              <i className="fas fa-laptop"></i>
                              {"      "}
                              <span>App Design cho máy yếu</span>
                         </NavLink>
                         <NavLink
                              onClick={handleShowRemove}
                              exact
                              activeClassName="active1"
                              to="/kythuat/tips"
                              className="skill__button w"
                         >
                              <i className="fas fa-feather-alt"></i> {"    "}
                              <span>Thủ Thuật Photoshop CS6</span>
                         </NavLink>
                         {/* <NavLink
                         exact
                         activeClassName="active1"
                         to="/kythuat/silk"
                         className="skill__button w"
                    >
                         <i className="fas fa-paper-plane"></i> {"    "}
                         <span>Tạo ra tác phẩm nghệ thuật</span>
                    </NavLink> */}
                    </div>
               </div>
          </>
     );
}
