import React from "react";
import "./Navside.scss";
import Text from "../TEXT";
import { NavLink, Link } from "react-router-dom";
export default function Navside() {
     const handleNavSideClick = (e, index) => {
          window.scroll(0, 0);
          document.title = Text.navSide[index].title;
     };
     const handlePlus = () => {
          document.title = "Kỹ thuật";
          const e = document.querySelector(".plus-icon");
          e.classList.toggle("active");
          console.log("add");
     };
     return (
          <div className="nav__side">
               <ul className="nav__side--list">
                    <li className="nav__side--list-item plus  ">
                         <i
                              onClick={handlePlus}
                              className="fas fa-plus-circle plus-icon"
                         ></i>
                         <ul className="nav__side-plus">
                              <li>
                                   <Link to="/kythuat">
                                        <i className="fas fa-sliders-h"></i>
                                        <span
                                             className="plus__item"
                                             onClick={handlePlus}
                                        >
                                             Ban kỹ thuật
                                        </span>
                                   </Link>
                              </li>
                              <li>
                                   <a
                                        className="nav__drive"
                                        href="https://drive.google.com/drive/folders/1utO4-JvieokBnEmLrWVC2f-PsYJa_kxF?usp=sharing"
                                        target="_blank"
                                        rel="noReferrer"
                                   >
                                        <i className="fab fa-google-drive"></i>
                                        <span onClick={handlePlus}>
                                             {"  "} Drive Mầm
                                        </span>
                                   </a>
                              </li>
                              {/* <li>
                                   <i className="fas fa-file-alt"></i>
                                   Tài liệu
                              </li>
                              <li>
                                   <i className="fas fa-file-alt"></i>
                                   Tài liệu
                              </li> */}
                         </ul>
                    </li>

                    {Text.navSide.map((nav, index) => (
                         <NavLink
                              key={index}
                              exact={nav.exact || false}
                              onClick={(e) => handleNavSideClick(e, index)}
                              activeClassName="activeNav"
                              className="nav__side--list-item"
                              to={nav.id}
                         >
                              <i className={nav.icon}></i>
                              <span>{nav.title}</span>
                         </NavLink>
                    ))}
               </ul>
          </div>
     );
}
