import React, { useEffect, useState } from "react";
import Heading from "../Heading/Heading";
import LoadImg from "../LoadImg/LoadImg";
import "./MemoryImg.scss";
import { handleOnload, handleReLoad } from "../Functions";
import { LazyLoadImage } from "react-lazy-load-image-component";
import LINK__IMG from "../LINK__IMG";
import { NavLink } from "react-router-dom";

export default function MemoryImg({ title, sub, id, MemoryImg, num }) {
     var i = 0;
     const [numImg, setNumImg] = useState(num || 9);
     const [value, setValue] = useState("");

     useEffect(() => {
          setNumImg(num || 9);
          return () => {
               const moreBtn = document.querySelector(".show__more--btn");
               const Img = document.querySelectorAll(".me__box img");
               handleReLoad();
               Img.forEach((item) => {
                    item.style.opacity = "0";
               });
               moreBtn && (moreBtn.innerText = "Xem thêm ảnh");
          };
     }, [num]);
     const newMemoryImg = [...MemoryImg].reverse();
     const EventImg = newMemoryImg.splice(0, numImg);

     const handleShowMore = () => {
          setNumImg(numImg + 9);
          const moreBtn = document.querySelector(".show__more--btn");
          if (newMemoryImg.length < 9) {
               moreBtn.innerText = "Hết rồi kiếm hình đi gòi up !!!";
          }
     };

     const handleNextImg = () => {
          i++;
          if (i >= EventImg.length - 1) {
               i = 0;
          }
          const imgShow = document.querySelector(".me__full--show-item");
          imgShow && imgShow.setAttribute("src", EventImg[i].link);
     };
     const handlePrevImg = () => {
          i--;
          if (i < 0) {
               i = EventImg.length - 1;
          }
          const imgShow = document.querySelector(".me__full--show-item");
          imgShow && imgShow.setAttribute("src", EventImg[i].link);
     };
     const handleImageClose = () => {
          const showContainer = document.querySelector(".me__full--show");
          showContainer && (showContainer.style.display = "none");
     };
     const handleImageClick = (item, index) => {
          const imgShow = document.querySelector(".me__full--show-item");
          const showContainer = document.querySelector(".me__full--show");
          imgShow.setAttribute("src", item.link);
          showContainer.style.display = "flex";
          i = index;
     };
     const handleSearch = (e) => {
          const s = document.querySelector(".me__search--item");
          if (value.length < 2) {
               s && s.classList.toggle("active");
          } else {
               s && s.classList.add("active");
               const c = document.querySelectorAll(".me__link");
               EVENT_NAME.forEach((item, index) => {
                    if (
                         item.title.includes(value) ||
                         item.title
                              .toLocaleLowerCase()
                              .includes(value.toLocaleLowerCase()) ||
                         removeAccents(item.title).includes(
                              value.toLocaleLowerCase(),
                         ) ||
                         removeAccents(item.title)
                              .toLocaleLowerCase()
                              .includes(value.toLocaleLowerCase())
                    ) {
                         c[index] && c[index].click();
                    }
               });
          }
     };
     // const keyDown = (e) => {
     //      e.preventDefault();
     //      switch (e.which) {
     //           case 39:
     //                handleNextImg();
     //                break;
     //           case 40:
     //                handleNextImg();
     //                break;

     //           case 37:
     //                handlePrevImg();
     //                break;
     //           case 38:
     //                handlePrevImg();
     //                break;
     //           case 27:
     //                handleImageClose();
     //                break;
     //           default:
     //                console.log("1");
     //                break;
     //      }
     // };
     function removeAccents(str) {
          let a = str
               .normalize("NFD")
               .replace(/[\u0300-\u036f]/g, "")
               .replace(/đ/g, "d")
               .replace(/Đ/g, "D");
          return a;
     }
     const EVENT_NAME = [...LINK__IMG.activityImg, ...LINK__IMG.activityClub];
     const handleSubmit = (e) => {};
     const handleKeyDown = (e) => {
          console.log(e.which);
          if (e.which === 13) {
               const c = document.querySelectorAll(".me__link");
               EVENT_NAME.forEach((item, index) => {
                    if (
                         item.title.includes(value) ||
                         item.title
                              .toLocaleLowerCase()
                              .includes(value.toLocaleLowerCase()) ||
                         removeAccents(item.title).includes(
                              value.toLocaleLowerCase(),
                         ) ||
                         removeAccents(item.title)
                              .toLocaleLowerCase()
                              .includes(value.toLocaleLowerCase())
                    ) {
                         c[index] && c[index].click();
                    }
               });
          }
     };
     return (
          <div id={id} className="me__full">
               {EVENT_NAME.map((item, index) => (
                    <NavLink
                         key={index}
                         className="me__link"
                         to={item.path}
                    ></NavLink>
               ))}
               <Heading sub={sub} id={id} title={title}></Heading>
               <span className="me__search">
                    <input
                         onKeyDown={handleKeyDown}
                         value={value}
                         onChange={(e) => setValue(e.target.value)}
                         className="me__search--item"
                    />
                    <i onClick={handleSearch} className="fas fa-search"></i>
                    <u onClick={handleSubmit} className="search--submit"></u>
               </span>
               <div className="me__container">
                    {EventImg.map((item, index) => (
                         <div
                              key={index}
                              onClick={(e) => handleImageClick(item, index)}
                              className="me__box"
                         >
                              <LoadImg />
                              <LazyLoadImage
                                   onLoad={(e) => handleOnload(e, index)}
                                   src={item.link}
                                   alt="Lỗi link ha gì goy coi hình khác đi"
                              />
                         </div>
                    ))}
               </div>
               <div className="show__more">
                    <button
                         className="show__more--btn"
                         onClick={() => handleShowMore()}
                    >
                         Xem thêm ảnh
                    </button>
               </div>

               {/* SHOW IMG ON CLICK AND NEXT || PREV */}
               <div className="me__full--show">
                    <div
                         onClick={(e) => handleImageClose(e)}
                         className="me__full--overlay"
                    ></div>
                    <span
                         onClick={(e) => handleImageClose(e)}
                         className="me__full--close"
                    >
                         <i className="fas fa-times-circle"></i>
                    </span>

                    <img className="me__full--show-item" src="" alt="a" />
                    <button onClick={handlePrevImg} className="prev__img">
                         <i className="fas fa-chevron-circle-left"></i>
                    </button>
                    <button
                         // onKeyDown={keyDown}
                         onClick={handleNextImg}
                         className="next__img"
                    >
                         <i className="fas fa-chevron-circle-right"></i>
                    </button>
               </div>
          </div>
     );
}
