import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./Cs6.scss";
export default function Cs6({ CourseList }) {
     const [render, setRender] = useState(() => {
          const PTS = JSON.parse(window.localStorage.getItem("render"));
          return PTS || [];
     });

     const handleGim = (e, course, index) => {
          const b = document.querySelectorAll(".cs__container");
          b[index].classList.toggle("clicked");
          // b[0].classList.toggle(`order${index + 1}`);
          b[index].classList.toggle("order1");
          if (render.indexOf(index) > -1) {
               const r = render.filter((x) => x !== index);
               setRender(r);
          } else {
               setRender([...render, index]);
          }
          // _____________________________________
     };

     useEffect(() => {
          render.forEach((item) => {
               const b = document.querySelectorAll(".cs__container");
               b[item].classList.add("clicked");
               b[item].classList.add("order1");
               b[0].classList.add(`order${item + 1}`);
          });
          let a = JSON.stringify(render);
          window.localStorage.setItem("render", a);
     });
     useEffect(() => {
          const PTS = JSON.parse(window.localStorage.getItem("PTS"));
          const AI = JSON.parse(window.localStorage.getItem("AI"));
          const PR = JSON.parse(window.localStorage.getItem("PR"));
          const AE = JSON.parse(window.localStorage.getItem("AE"));
          const AU = JSON.parse(window.localStorage.getItem("AU"));
          const LR = JSON.parse(window.localStorage.getItem("LR"));
          let W0 = ~~((PTS * 100) / (CourseList.courseYoutubePTS.length - 1)); // pts
          let W1 = ~~((AI * 100) / (CourseList.courseYoutubeAI.length - 1)); // ai
          let W2 = ~~((PR * 100) / (CourseList.courseYoutubePR.length - 1)); // pr
          let W3 = ~~((AE * 100) / (CourseList.courseYoutubeAE.length - 1)); // ae
          let W4 = ~~((AU * 100) / (CourseList.courseYoutubeAU.length - 1)); // au
          let W5 = ~~((LR * 100) / (CourseList.courseYoutubeLR.length - 1)); // cv
          let MyW = [W0, W1, W2, W3, W4, W5];
          // START &&

          MyW.forEach((item, i) => {
               const spanWithd = document.querySelectorAll(".cs__img span");
               spanWithd[i + 1] && (spanWithd[i + 1].style.width = `${item}%`);
               if (item < 100) {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i + 1] && (b[i + 1].innerText = `${item}%`);
               } else {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i + 1] && (b[i + 1].innerText = "Đã hoàn thành");
               }
          });
     });

     return (
          <div className="cs__full">
               <p className="cs__start">
                    <i className="fas fa-play"></i>
                    <span>Bắt đầu một khoá học mới tại đây</span>
               </p>
               <div className="cs__oderbox">
                    {/* <button className="cs__delstart">xoá ghim</button> */}
                    {CourseList.courseName.map((course, index) => (
                         <div
                              className="cs__container clickfade"
                              key={index}
                              title={course.name}
                         >
                              <i
                                   onClick={(e) => handleGim(e, course, index)}
                                   className="fas fa-star cs__mask"
                                   title="Vào học đi kìa"
                              ></i>
                              <Link to={course.to} className="cs__img">
                                   <img src={course.srcImg} alt="lỗi ảnh" />
                                   <span></span>
                                   <b>0%</b>
                              </Link>

                              <p className="cs__content">{course.name}</p>

                              {/*
                              // href="https://www.youtube.com/watch?v=C6m_QYL-Lt0&list=PLH88ngaKjRaQdJkYizDyCE_5Bpcfk9DEO"
                              // target="_blank"
                              // rel="noReferrer"
                              className="cs__btn"
                         >
                              <i className="fas fa-pen-alt"></i>
                              <span>Học ngay</span>
                         </Link> */}
                         </div>
                    ))}
               </div>
          </div>
     );
}
