import React from "react";
import "./AvatarCover.scss";
import Text from "../TEXT";
export default function AvatarCover() {
     var i = 0;
     const handlePrev = (index) => {
          const avatar = document.querySelectorAll(".avatar__item");
          const imgs = avatar[index].querySelectorAll("img");
          i--;
          if (i <= 0) {
               i = 0;
          }

          imgs.forEach((img) => {
               img.style.transform = `translateX(-${i}00%)`;
          });
     };
     const handleNext = (index) => {
          const avatar = document.querySelectorAll(".avatar__item");
          const imgs = avatar[index].querySelectorAll("img");
          i++;
          if (i >= imgs.length - 3) {
               i = imgs.length - 3;
          }

          imgs.forEach((img) => {
               img.style.transform = `translateX(-${i}00%)`;
          });
     };
     return (
          <div className="avatar__container">
               <div className="avatar__scroll">
                    {Text.AvatarCover.map((item, index) => (
                         <div key={index} className="avatar__avatar">
                              <div className="avatar__button">
                                   <div
                                        onClick={(e) => handlePrev(index)}
                                        className="avatar__button-item"
                                   >
                                        <i className="fas fa-chevron-left"></i>
                                   </div>
                                   <div
                                        onClick={(e) => handleNext(index)}
                                        className="avatar__button-item"
                                   >
                                        <i className="fas fa-chevron-right"></i>
                                   </div>
                              </div>
                              <p>{item.name}</p>
                              <div>
                                   <div className="avatar__item">
                                        {item.link.map((img, index) => (
                                             <img
                                                  key={index}
                                                  src={img}
                                                  alt="lỗi ảnh"
                                             />
                                        ))}
                                   </div>
                              </div>
                         </div>
                    ))}
               </div>
          </div>
     );
}
