import React from "react";
import { NavLink } from "react-router-dom";
import "./EventName.scss";
import LINK__IMG from "../LINK__IMG";
import { useState } from "react";
export default function EventName() {
     const [num, setNum] = useState(6);
     const [type, setType] = useState("down");

     const EVENT_NAME = [...LINK__IMG.activityImg, ...LINK__IMG.activityClub].splice(
          -num,
     );

     const handleClick = (e) => {
          if (num === 6) {
               setNum(20);
               setType("up");
          }
          if (num === 20) {
               setNum(6);
               setType("down");
          }
          return;
     };
     return (
          <div>
               <div className="e__event">
                    {/* <NavLink
                         to="/Images"
                         onClick={() => (document.title = "Hình ảnh")}
                         className="e__event__container"
                         exact
                         activeClassName="activeEvent"
                    >
                         Hình ảnh
                    </NavLink> */}
                    {EVENT_NAME.map((e, index) => (
                         <NavLink
                              to={e.path}
                              onClick={() => (document.title = e.title)}
                              key={index}
                              className="e__event__container"
                              exact
                              activeClassName="activeEvent"
                         >
                              {/* <NavLink to={e.id} className="e__event-name"> */}
                              {e.title}
                              {/* </NavLink> */}
                         </NavLink>
                    ))}
               </div>
               <div onClick={() => handleClick()} className="event__show">
                    <i className={`fas fa-chevron-${type}`}></i>
               </div>
          </div>
     );
}
