import React from "react";
import "./LogoPNG.scss";
import Text from "../TEXT";
export default function LogoPNG() {
     return (
          <div className="logo__container">
               <div className="logo__scroll">
                    {Text.LogoPNG.map((logo, index) => (
                         <div key={index} className="logo__png">
                              <div className="logo__item">
                                   <img src={logo.linkUI} alt="a" />
                                   <p>{logo.name}</p>
                              </div>

                              <div className="logo__icon">
                                   <a
                                        href={logo.linkDown}
                                        target="_blank"
                                        rel="noReferrer"
                                   >
                                        <i
                                             title="Nhấn để tải về"
                                             className="fas fa-download"
                                        ></i>
                                   </a>
                              </div>
                         </div>
                    ))}
               </div>
          </div>
     );
}
